import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather";
import Can from '../../../../../utility/permissions/can';
class BusinessRegistrationTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Busienss Name",
                selector: "BUSINESS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESS_NAME}</p>
            },
            {
                name: "Busienss Type Name",
                selector: "BUSINESS_TYPE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESSTYPE.BUSINESS_TYPE_NAME}</p>
            },
            {
                name: "Document Type",
                selector: "DOCUMENT_TYPE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESS_DOCUMENT_TYPE.DOCUMENT_TYPE_NAME}</p>
            },
            {
                name: "SubCounty",
                selector: "SUBCOUNTY_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SUB_COUNTY.SUBCOUNTY_NAME}</p>
            },
            {
                name: "Ward",
                selector: "Ward",
                sortable: true,
                cell: row => <p className="mb-0">{row.WARD.WARD_NAME}</p>
            },
            {
                name: "Business Category",
                selector: "FEECATEGORY_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEE_CATEGORY.FEECATEGORY_NAME}</p>
            },
            {
                name: "Detailed Description",
                selector: "FEE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEE.FEE_NAME}</p>
            },
            {
                name: "Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYSTEMSTATUS.SYS_STATUS_NAME}</p>
            }
        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    responsive
                    subHeaderComponent={
                        <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShowTabs} handleFilter={this.props.handleFilter} />

                    }
                />
            </>

        )
    }
}


export default BusinessRegistrationTable